"use server";

import { cookies } from "next/headers";
import { AUTH_TOKEN_COOKIE_KEY } from "../constants";

export interface ApiError {
  status: number;
  message: string;
  data?: null;
}

export interface GenericResponse<T> {
  success: boolean;
  data: T;
  message: string | null;
}

const getBackendApi = () => {
  if (process.env.NEXT_PUBLIC_API_URL) {
    return `${process.env.NEXT_PUBLIC_API_URL}/api`;
  }
  throw new Error("NEXT_PUBLIC_API_URL is not defined");
};

const BASE_URL = getBackendApi();

/**
 * Helper to get jwt token from cookie
 */
const getJWTToken = async () => {
  try {
    const cookieStorage = await cookies();
    return cookieStorage.get(AUTH_TOKEN_COOKIE_KEY)?.value || null;
  } catch (error) {
    console.warn("Error fetching auth token from cookie", error);
    return null;
  }
};

export async function buildQueryString(
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  params: Record<string, any>
): Promise<string> {
  const query = new URLSearchParams();
  Object.entries(params).forEach(([key, value]) => {
    if (value !== undefined && value !== null) {
      query.append(key, String(value));
    }
  });
  return query.toString();
}

/**
 * Generic GET request
 */
export async function get<T>(
  endpoint: string,
  headers?: Record<string, string>
): Promise<T> {
  const token = await getJWTToken();
  const response = await fetch(`${BASE_URL}${endpoint}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      ...headers,
      ...(token ? { Authorization: `Bearer ${token}` } : {}),
    },
  });
  return handleResponse<T>(response);
}

/**
 * Generic POST request
 */
export async function post<T>(
  endpoint: string,
  body: unknown,
  contentType: "application/json" | "multipart/form-data" = "application/json",
  headers?: Record<string, string>
): Promise<T> {
  const token = await getJWTToken();

  const finalHeaders: Record<string, string> = {
    ...headers,
    ...(token ? { Authorization: `Bearer ${token}` } : {}),
  };

  if (contentType == "application/json") {
    finalHeaders["Content-Type"] = "application/json";
  }

  const response = await fetch(`${BASE_URL}${endpoint}`, {
    method: "POST",
    headers: finalHeaders,
    body:
      contentType == "multipart/form-data"
        ? (body as FormData)
        : JSON.stringify(body),
  });

  return handleResponse<T>(response);
}

/**
 * Generic PUT request
 */
export async function put<T>(
  endpoint: string,
  body: unknown,
  param: string,
  headers?: Record<string, string>
): Promise<T> {
  const token = await getJWTToken();
  const response = await fetch(`${BASE_URL}${endpoint}/${param}`, {
    method: "PUT",
    headers: {
      "Content-Type": "application/json",
      ...headers,
      ...(token ? { Authorization: `Bearer ${token}` } : {}),
    },
    body: JSON.stringify(body),
  });
  return handleResponse<T>(response);
}

/**
 * Generic delete request
 */
export async function destroy<T>(
  endpoint: string,
  param: string,
  headers?: Record<string, string>
): Promise<T> {
  const token = await getJWTToken();
  const response = await fetch(`${BASE_URL}${endpoint}/${param}`, {
    method: "DELETE",
    headers: {
      "Content-Type": "application/json",
      ...headers,
      ...(token ? { Authorization: `Bearer ${token}` } : {}),
    },
  });
  return handleResponse<T>(response);
}

/**
 * Handle fetch response
 */
async function handleResponse<T>(response: Response): Promise<T> {
  const contentType = response.headers.get("content-type") || "";

  console.info("Response URL: ", response.url);

  let data;
  if (contentType.includes("application/json")) {
    data = await response.json();
  } else {
    data = await response.blob();
  }
  if (!response.ok) {
    const error: ApiError = {
      status: response.status,
      message: (data && data.message) || response.statusText,
      data,
    };
    throw error;
  }
  return data;
}
